<template>
    <div class="tag-wrapper">
        <span class="tag" v-if="!!is_first">首单专享</span>
        <span class="tag" v-if="!!is_new">新品</span>
        <span class="tag tag-gray" v-if="presale_text">{{presale_text}}</span>
        <span class="tag" v-if="buy_limit">限时</span>
        <span class="tag" v-if="buy_limit">优惠限{{buy_limit}}件</span>
        <span class="tag" v-if="text">{{text}}</span>
        <div class="activity-tag" v-if="activity.length">
            <span v-for="(tag, i) in activity" :key="i">
                <span v-if="(tag.type === 9 && isNnTag) || tag.type !== 9" class="tag">{{tag.tag||tag.type_name}}</span>
            </span>
        </div>
    </div>
</template>

<script>
    import {compVer} from '@/utils/tools'
    import {platform} from "@/utils/platform";
    export default {
        name: 'tag',
        props: {
            is_first: Number,
            is_new: Number,         //mark_new
            is_presale: Number,
            presale_text: String,   //presale_delivery_date_display
            is_promotion: Number,
            buy_limit: Number,
            text: String,
            activity: {
                type: Array,
                default: ()=>{}
            }
        },
        computed:{
            //是否支持N元N件
            isNnTag() {
                return compVer(platform.appVersion, '8.29.0') >= 0;
            }
        }
    };
</script>

