<template>
    <div class="coupon-part" @click="_eventCoupon">
        <div class="coupon-part-content">
            <span class="coupon-get" v-if="coupon.is_receive"></span>
            <div class="coupon-money">
                <span>￥</span>
                <span class="money">{{coupon.money}}</span>
            </div>
            <div class="coupon-condition">满{{coupon.pay_min}}元使用</div>
            <div class="coupon-desc">{{coupon.description}}</div>
        </div>
        <div class="coupon-part-btns">
            <div class="coupon-btn-use" v-if="coupon.is_receive">去使用</div>
            <div class="coupon-btn-get" v-if="!coupon.is_receive">立即领取</div>
        </div>
    </div>
</template>

<script>
    export default {
        props: ['coupon','options'],
        methods: {
            _eventCoupon(){
                let {is_receive} = this.coupon;
                if(is_receive){
                    this._eventUseCoupon();
                }else{
                    this._eventGetCoupon();
                }
            },
            _eventUseCoupon(){
                this.log({
                    cid: 'my_vip',
                    aid: 'coupon'
                });
                this.$emit('clickUseCoupon',this.coupon,this.options)
            },
            _eventGetCoupon(){
                this.log({
                    cid: 'my_vip',
                    aid: 'coupon'
                });
                this.$emit('clickGetCoupon',this.coupon,this.options)
            }
        }
    }
</script>

