<template>
    <div>
        <div class="privilege-content">
            <div class="content-list-part" v-for="(privilege, i) in privilegeData" :key="i"
                 @click="_eventSelect({privilege,privilege_index:i})">
                <div class="part-icon">
                    <icon-font :name="privilege.icon" size="80" :color="(privilege_index===i?'#3CB963':'#666666')"/>
                    <div class="name">{{privilege.title}}</div>
                    <div class="desc">{{privilege.desc}}</div>
                    <div class="test" v-if="privilege.test">内测</div>
                </div>
            </div>
        </div>
        <div class="privilege-detail" v-if="privilege_text.length">
            <icon-font name="delete" class="detail-close" @click="_eventCloseDetail"/>
            <span v-for="(o, a) in privilege_text" :key="a">{{o}}</span>
        </div>
    </div>
</template>

<script>
    export default {
        props: ['isVip', 'vipPointRate', 'friends_card'],
        data() {
            return {
                privilege_text: [],
                privilege_index: null
            }
        },
        computed: {
            privilegeData() {
                let privilege = [
                    {
                        icon: 'lvqiazhuanxiangquanx',
                        title: '专享券',
                        desc: '不低于292元/月',
                        info: ['开通绿卡后每天专享券领不停；', '每周还可领取额外专享券']
                    },
                    {
                        icon: 'zhuanxiangtejiax',
                        title: '专享特价',
                        desc: '每周五更新',
                        info: ['每周大量绿卡专享特价商品；', '请留意绿卡价标识']
                    },
                    {
                        icon: 'beijifenx',
                        title: `${this.vipPointRate || 2}倍积分`,
                        desc: '积分可抵现',
                        info: [`购物根据实付金额返${this.vipPointRate || 2}倍积分`]
                    },
                    {
                        icon: 'gengduotequanx',
                        title: '更多特权',
                        desc: '敬请期待',
                        info: ['更多特权敬请期待']
                    }
                ];

                if (this.friends_card) {
                    let {config_nums} = this.friends_card;
                    privilege.splice(3, 0, {
                        icon: 'qinyoukax',
                        title: `亲友卡x${config_nums}`,
                        desc: `亲友免费领`,
                        info: ['开通年卡，即可获得亲友卡','赠送给亲友后，亲友可获得相应的绿卡体验期'],
                        test: true
                    },)
                }

                return privilege;
            }
        },
        methods: {
            _eventSelect(params) {
                const {privilege, privilege_index} = params;
                this.privilege_text = privilege.info;
                this.privilege_index = privilege_index;
                this.log({
                    cid: 'my_vip',
                    aid: 'my_privilege',
                    index: this.isVip
                });
            },
            _eventCloseDetail() {
                this.privilege_text = [];
                this.privilege_index = null;
            }
        }
    }
</script>

