<!--目前用于vip页-->
<template>
    <div class="product-wrapper" v-if="product" @click="_eventDetail">
        <div class="image-wrapper">
            <img v-lazy="product.small_image" alt="" class="image" :key="product.small_image"/>
            <div v-if="priceInfo.notice" class="notice-gray">
                <span>{{priceInfo.notice}}</span>
            </div>
        </div>
        <div class="info-wrapper">
            <div class="name-wrapper">
                <div class="name">
                    <span class="tag tag-auto" v-if="product.is_presale">预售</span>
                    <span class="tag tag-auto" v-if="priceInfo.isCanBook && product.today_stockout">{{product.today_stockout}}</span>
                    <span class="name-text">{{product.product_name}}</span>
                </div>
                <div class="spec">{{product.spec}}</div>
                <div class="tags-wrapper">
                    <tag :is_new="product.mark_new"
                         :is_presale="product.is_presale"
                         :presale_text="product.presale_delivery_date_display"
                         :is_promotion="product.is_promotion"
                         :buy_limit="product.buy_limit"
                         :activity="product.activity"/>
                </div>
            </div>
            <div class="price-wrapper">
                <div class="price-info">
                    <div class="price-part">
                        <div class="price type1" v-if="mode === 1">￥<span>{{product.price}}</span></div>
                        <div class="price" v-if="mode !== 1">￥<span class="f34">{{priceInfo.priceNum[0]}}</span>.{{priceInfo.priceNum[1]}}
                        </div>
                        <div class="origin-price" v-if="priceInfo.showOrigin && (mode !== 1)">
                            ￥{{priceInfo.originPrice}}
                        </div>
                    </div>
                    <div class="vip-price type-new" v-if="product.vip_price">
                        <div class="vip-price-info">￥<span class="f34">{{priceInfo.vipPriceNum[0]}}</span>.{{priceInfo.vipPriceNum[1]}}
                        </div>
                        <span class="vip-price-tag-two"></span>
                    </div>
                </div>
                <div class="icon-cart-wrapper" v-if="!hideCart">
                    <div class="icon-cart-border" @click.stop="_eventClickCart">
                        <icon-font name="gouwuchex" size="32" class="icon-cart" :color="priceInfo.cartEnable?'#3CB963':'#EDEDED'"/>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    /**
     * 目前只支持在app端复用，h5暂无商品延伸功能
     */
    import Tag from '@/components/Tag'
    import {bridge} from '@/utils/native/bridge'
    import {compVer} from '@/utils/tools'
    import {platform} from "@/utils/platform";

    export default {
        components: {
            Tag
        },
        props: {
            product: Object,
            //mode:模版样式种类 0:默认 1:vip主题
            mode: {
                default: 0
            },
            //isVip:是否是vip用户 true:默认 (vip商品特有、其他请忽略)
            isVip: {
                default: true
            },
            hideCart: {
                default: false
            }
        },
        computed: {
            priceInfo() {
                const {price, origin_price, vip_price, stock_number, is_booking} = this.product;
                let showOrigin = price < origin_price && origin_price > 0;
                let originPrice = Number(origin_price).toFixed(2);
                let priceNum = Number(price).toFixed(2).split('.');
                let vipPriceNum = Number(vip_price).toFixed(2).split('.');
                let cartEnable = stock_number >= 1 && this.isVip;
                let isCanBook = compVer(platform.appVersion, '8.28.0') >= 0;
                let notice = stock_number < 1 ? '抢光了' : '';
                if(is_booking){
                    if(isCanBook){
                        notice = '今日售罄';
                    }else{
                        notice = '抢光了';
                        cartEnable = false;
                    }
                }

                return {
                    showOrigin,
                    originPrice,
                    priceNum,
                    vipPriceNum,
                    cartEnable,
                    notice,
                    isCanBook
                }
            },
        },
        methods: {
            _eventDetail() {
                const {id, product_name} = this.product;
                this.log({
                    cid: 'my_vip',
                    aid: 'product_detail',
                    value: id,
                    name: product_name || '',
                    index: this.isVip
                });
                this.openUrl({path: 'ddxq://product/detail', query: {id}})
            },
            _eventClickCart() {
                const {status, stock_number, product_name, id} = this.product;
                const {cartEnable} = this.priceInfo;
                this.log({
                    cid: 'my_vip',
                    aid: 'add_product',
                    value: id,
                    name: product_name || '',
                    index: this.isVip
                });
                if (!cartEnable) {
                    return;
                }
                if (status === 0) {
                    this.$toast('商品已经下架了');
                    return;
                }
                if (stock_number < 1) {
                    this.$toast('商品已被抢光了');
                    return;
                }
                this._addProduct();
            },
            //添加商品 暂仅支持app添加
            _addProduct() {
                const _that = this;
                const product = this.product;
                let {id, sizes} = product;
                if (sizes && sizes.length > 0) {
                    if (compVer(platform.appVersion, '8.20.0') > 0) {
                        bridge.addProductToCartSize({
                            product: product,
                            callback: (r) => {
                                if (r.success) {
                                    _that.$toast('添加成功', 1500)
                                }
                            }
                        });
                    } else {
                        _that.$toast('请升级到最新版APP', 1500)
                    }
                } else {
                    bridge.addProductToCart({
                        id, count: 1, sizes: [], callback: (res) => {
                            const {success} = res;
                            if (success) {
                                _that.$toast('添加成功', 1500)
                            }
                        }
                    });
                }
            }
        }
    }
</script>

