<template>
    <div class="vip-wrapper" :class="{pos: (card_continue || (status !== 1 && card_float))}" ref="domVip">
        <div class="vip-not-banner" v-if="status === 0 && !!load_banner">
            <div class="vip-not-img" :style="styleBanner">
                <img v-lazy="load_banner" alt=""/>
            </div>
        </div>

        <div class="vip-banner" v-if="status !== 0">
            <div class="banner-content">
                <div class="user-info">
                    <div class="avatar">
                        <img class="image" :src="user.avatar || default_avatar" alt=""/>
                    </div>
                    <div class="desc">
                        <div class="desc-user">
                            <span class="name">{{user.username}}</span>
                        </div>
                        <div class="desc-wrap">
                            <div class="status" v-if="status === 0">当前未开通</div>
                            <div class="desc-date" v-if="status === 1">{{expire_time}}到期</div>
                            <div class="status" v-if="status === 2">已到期</div>
                        </div>
                    </div>
                    <div class="suggest-into" v-if="!!renew_remain" @click="eventVipPay({position: 'head'})">
                        <span class="vip-icon-card"></span>
                        <div class="suggest-text">{{card_rebate}}续卡</div>
                        <icon-font name="Jiantou" color="#ffffff" size="28" class="suggest-icon"/>
                    </div>
                </div>
                <div class="save-money" v-if="status !== 0">
                    已为您节省<span>{{save_money}}</span>元
                    <icon-font name="jisuanqi" color="#2E3748" size="30" @click="eventSaveChange"/>
                </div>
                <div class="save-money" v-if="status === 0">开通绿卡预计平均每单省<span>6.96</span>元</div>
                <div class="already-special">
                    <div class="td-part">
                        <span>已领券</span>
                        <span><strong>{{ticket_num}}</strong>张</span>
                    </div>
                    <div class="td-part">
                        <span>已返积分</span>
                        <span><strong>{{point_num}}</strong></span>
                    </div>
                    <div class="td-part">
                        <span>已享特价商品</span>
                        <span><strong>{{product_num}}</strong>件</span>
                    </div>
                </div>

                <pop-box :show="showSave" @clickClosePop="eventSaveChange">
                    <div class="save-content">
                        <div class="save-title">已为您节省总计</div>
                        <div class="save-price-all">￥{{save_money}}</div>
                        <div class="save-list">
                            <div class="save-part">
                                <span>绿卡专享价</span>
                                <span>￥{{save_product_money}}</span>
                            </div>
                            <div class="save-part">
                                <span>绿卡专享优惠券</span>
                                <span>￥{{save_coupon_money}}</span>
                            </div>
                            <div class="save-part">
                                <span>购物额外返积分</span>
                                <span>￥{{save_point_money}}</span>
                            </div>
                        </div>
                    </div>
                </pop-box>
            </div>
        </div>

        <div class="vip-privilege">
            <div class="vip-title-one">绿卡特权</div>
            <privilege-list :isVip="is_vip" :vipPointRate="vip_point_rate" :friends_card="friends_card"></privilege-list>
        </div>

        <div class="vip-open-up" :class="{top:is_activity}" v-if="status !== 1">
            <div class="vip-open-up-btn" @click="eventVipPay({position: 'head'})">
                <span class="open-up-tag" v-if="is_activity">{{user_status === 1 ? '限时首购专享' : '限时特惠'}}</span>
                <span class="open-up-text">{{card_rebate?card_rebate:'立即'}}开通绿卡</span>
            </div>
        </div>

        <div class="vip-friends m-top20" v-if="friends_card">
            <div class="friends-mask" v-show="showGuide" @click="eventGuideMask"></div>
            <div class="friends-part" :class="{'position':showGuide}">
                <div class="vip-title-one">
                    <span>亲友卡</span>
                </div>
                <div class="vip-title-small">
                    <span>还有{{friends_card.can_send_nums}}张亲友卡待送出</span>
                </div>
                <div class="friends-send" @click.stop="eventGuideSend">{{friends_card.can_send_nums?'去赠送':'去查看'}}</div>
                <div class="guide-title" v-if="showGuide">
                    <icon-font name="CombinedShapex" size="60" color="#ffffff"/>
                    <p class="guide-text">点这里去赠送亲友卡</p>
                </div>
            </div>
        </div>

        <div class="vip-coupons m-top20" v-if="today_ticket.tickets || week_ticket.tickets" ref="domCoupons">
            <div class="vip-title-one" v-if="status !== 1">
                <icon-font name="icon-test" color="#3CB963" class="title-tag"/>
                <span>绿卡专享券 每月不低于292元</span>
            </div>
            <div class="vip-title-one" v-if="status === 1">
                <span>绿卡专享券</span>
            </div>
            <div class="coupon-today" v-if="today_ticket.tickets">
                <div class="vip-title-small">
                    <span>今日专享券</span>
                    {{today_ticket.notice}}
                </div>
                <div class="coupon-list">
                    <coupon-item v-for="(ticket, x) in today_ticket.tickets" :coupon="ticket"
                                 :options="{type:1,index:x}" :key="x" @clickUseCoupon="eventUseCoupon"
                                 @clickGetCoupon="eventGetCoupon"></coupon-item>
                </div>
            </div>
            <div class="week-today" v-if="week_ticket.tickets">
                <div class="vip-title-small">
                    <span>本周专享券</span>
                    {{status === 1 ? week_ticket.notice : ''}}
                </div>
                <div class="coupon-list">
                    <coupon-item v-for="(ticket, x) in week_ticket.tickets" :coupon="ticket" :options="{type:2,index:x}"
                                 :key="x" @clickUseCoupon="eventUseCoupon"
                                 @clickGetCoupon="eventGetCoupon"></coupon-item>
                </div>
            </div>
        </div>

        <div class="vip-integral-speed m-top20" v-if="status !== 1">
            <div class="vip-title-one">
                <icon-font name="Group1" color="#3CB963" class="title-tag"/>
                <span>积分加速 可抵现</span>
            </div>
            <div class="speed-content">
                <div class="speed-part-banner">
                    <div class="text-part-left">
                        <span class="text-span">您当前购物</span>
                        <span class="text-span">返积分为<strong>1倍</strong></span>
                    </div>
                    <div class="text-part-right">
                        <span class="text-span">开通绿卡购物</span>
                        <span class="text-span">返积分为<strong>{{vip_point_rate}}倍</strong></span>
                    </div>
                </div>
                <div class="integral-info" @click="eventGoIntegral">
                    <span>积分说明</span>
                    <icon-font name="shuomingx" color="#999999" align="true"/>
                </div>
                <div class="vip-open-up-btn" @click="eventVipPay({position: 'speed'})">
                    <span class="open-up-text">立即开启积分加速</span>
                </div>
            </div>
        </div>

        <div class="vip-products m-top20">
            <div class="vip-title-one">
                <icon-font name="Group" color="#3CB963" v-if="status !== 1" class="title-tag"/>
                <span>绿卡专享特价</span>
            </div>
            <div class="tab-wrapper" ref="domTabProduct">
                <!--商品导航nav-->
                <div class="tab-content-box">
                    <tab-nav :tabList="tabs" @onSelectTab="onSelectTab" :class="{pos:tabs_float}"/>
                </div>
                <!--商品列表-->
                <div class="product-content">
                    <div v-if="products_list.length">
                        <lazy-component v-for="(product, i) in products_list" :key="i">
                            <product-item :product="product" :mode="1" :isVip="is_vip"></product-item>
                        </lazy-component>
                    </div>
                    <load-more :show="going"/>
                    <div class="product-empty" v-if="!products_list.length">该分类暂无绿卡特价商品</div>
                    <div class="product-no-more" v-if="!!products_list.length && !is_more">更多特权敬请期待...</div>
                    <div class="to-top-icon-wrapper" :class="{'to-bottom':!card_float}" v-if="tabs_float"
                         @click="animatePos(0)">
                        <icon-font name="huidaodingbu" size="34" color="#cccccc"/>
                    </div>
                </div>
            </div>
        </div>

        <div class="vip-bottom-ad" v-if="status !== 1 && card_float">
            <div class="bottom-ad-part">
                <span class="open-up-tag" v-if="is_activity">{{user_status === 1 ? '限时首购专享' : '限时特惠'}}</span>
                <div class="bottom-ad-text">
                    <span>{{card_name}}</span>
                    <span class="day" v-if="card_price">{{card_price}}元</span>
                    <s v-if="is_activity">{{card_origin_price}}元</s>
                    <span class="angle"/>
                </div>
                <span class="bottom-ad-btn" @click="eventVipPay({position:'bottom'})">开通绿卡</span>
            </div>
        </div>
        <div class="vip-bottom-ad" v-if="status === 1 && card_continue">
            <div class="bottom-ad-part">
                <span class="bottom-ad-btn" @click="eventVipPay({position:'bottom'})">{{card_rebate}}续卡</span>
                <div class="bottom-ad-text">
                    <span>距离绿卡到期还有</span>
                    <span class="day">{{renew_remain}}天</span>
                </div>
            </div>
        </div>

        <pop-box :show="showFriends" theme="1" @clickClosePop="eventFriendsChange">
            <div class="friends-pop">
                <div class="friends-title">尊敬的绿卡会员，恭喜你，获得亲友卡赠送内测资格！</div>
                <div class="friends-img"/>
                <div class="friends-send-btn" @click="eventFriends">立即体验</div>
            </div>
        </pop-box>
    </div>
</template>

<script>
    import API from '@/server/api'
    import PrivilegeList from '@/components/vip/PrivilegeList'
    import CouponItem from '@/components/vip/CouponItem'
    import ProductItem from '@/components/ProductItem'
    import PopBox from '@/components/PopBox'
    import TabNav from '@/components/TabNav'
    import LoadMore from '@/components/LoadMore'
    import {bridge} from '@/utils/native/bridge'
    import {platform} from '@/utils/platform'
    import {getQueryString} from '@/utils/tools'
    import {local} from '@/utils/storage'
    import {DEFAULT_USER_AVATAR_NEW} from '@/const/images'

    let navInterval = null;
    export default {
        name: 'vip',
        components: {
            PrivilegeList,
            CouponItem,
            ProductItem,
            TabNav,
            LoadMore,
            PopBox
        },
        data() {
            return {
                load_banner: '',
                default_avatar: DEFAULT_USER_AVATAR_NEW,
                username: '',
                card_rebate: '',
                card_price: '',

                tabs: [],
                tabs_index: 0,
                tabs_all_show: false,
                tabs_top: 0,
                tabs_float: false,
                products_list: [],
                product: {},
                page: 1,
                is_more: false,
                going: false,
                scroll_top: 0,

                user: {},
                expire_time: '',
                label: '',
                status: 0,      // 0-未申请 1-`  2-已过期
                user_status: 0, // 1-新用户 2-续费用户 3-已过期
                ticket_num: 0,
                point_num: 0,
                product_num: 0,
                save_money: '',
                today_ticket: {},
                week_ticket: {},

                cards: [],
                renew_remain: 0,
                vip_point_rate: 0,
                card_top: false,
                card_float: false,

                is_vip: false,
                is_activity: false,
                card_continue: false,
                card_name: '',
                card_origin_price: '',
                styleBanner: {},

                domVip: null,
                showSave: false,
                save_product_money: '', //节省特价商品金额
                save_coupon_money: '',  //节省优惠券金额
                save_point_money: '',   //额外积分金额
                invite_code: '', //小哥邀请码

                showFriends: false, //获得邀请卡资格
                showGuide: false,   //显示引导
                friends_card: ''    //亲友卡相关
            }
        },
        watch: {
            station_id_ready() {
                if (this.station_id_ready && this.user_login) {
                    this.requestVipDetail();
                }
            },
            user_login() {
                if (this.station_id_ready && this.user_login) {
                    this.requestVipDetail();
                }
            }
        },
        created() {
            window.addEventListener('scroll', this.eventVipScroll, true);
            this.activeScroll();
        },
        mounted() {
            let {invite_code} = this.$route.query;
            this.invite_code = invite_code || '';
            if (this.station_id_ready && this.user_login) {
                this.requestVipDetail();
            }
            this.setApp();
        },
        beforeDestroy() {
            clearInterval(navInterval);
            window.removeEventListener('scroll', this.eventVipScroll, true);
        },
        methods: {
            async requestVipDetail() {
                const {data} = await this.$http.get(API.vipApi('detail'));
                let {cards, cate, cate_detail, is_more, expire_time, load_banner, point_num, product_num, ticket_num, renew_remain, save_money, status, user_status, user_info, vip_point_rate, today_ticket, week_ticket, save_product_money, save_coupon_money, save_point_money, friends_card} = data;

                this.load_banner = load_banner;
                this.status = status;
                this.user_status = user_status;
                this.user = user_info;
                this.expire_time = expire_time;
                this.vip_point_rate = vip_point_rate;
                this.save_money = save_money;
                this.point_num = point_num;
                this.product_num = product_num;
                this.ticket_num = ticket_num;
                this.is_vip = status === 1;  //是否是会员
                this.today_ticket = today_ticket;
                this.week_ticket = week_ticket;
                this.tabs = cate;
                this.products_list = cate_detail;
                this.is_more = is_more;
                this.renew_remain = renew_remain;
                this.save_product_money = save_product_money;
                this.save_coupon_money = save_coupon_money;
                this.save_point_money = save_point_money;
                this.friends_card = friends_card;

                let {friends_guide} = local.load('VIP_FRIENDS');
                if(!friends_guide && !!friends_card){
                    local.save('VIP_FRIENDS', {friends_guide: true});
                    this.stopScroll();
                    this.showFriends = true;
                }

                this.log({
                    cid: 'my_vip',
                    aid: 'index',
                    index: status === 1
                });

                if (load_banner) {
                    let bannerW = getQueryString('width', load_banner);
                    let bannerH = getQueryString('height', load_banner);
                    let windowW = document.body.clientWidth;
                    this.styleBanner = {
                        height: `${windowW * bannerH / bannerW}px`
                    };
                }

                this.cardInfoUpdate(cards, renew_remain);
                this.waitInit(() => {
                    if (this.$refs.domCoupons) {
                        this.card_top = this.$refs.domCoupons.offsetTop;
                        this.tabs_top = this.$refs.domTabProduct.offsetTop;
                        this.domVip = this.$refs.domVip;
                    }
                    let {go_product} = this.$route.query;
                    if (go_product) {
                        this.animatePos(this.tabs_top)
                    }
                });
            },
            async requestVipProducts({page}) {
                const {data} = await this.$http.get(API.vipApi('product'), {page}, false);
                if (data) {
                    const {cate_detail, is_more} = data;
                    let products_list = this.products_list;
                    let new_products_list = [];
                    new_products_list = page > 1 ? products_list.concat(cate_detail) : cate_detail;
                    this.page = page;
                    this.is_more = is_more;
                    this.products_list = new_products_list;
                    this.going = false;
                }
            },
            async requestCateProducts(id) {
                const {data} = await this.$http.get(API.homeApi('categoriesdetail'), {
                    category_id: id,
                    is_vip: 1
                }, false);
                if (data) {
                    this.products_list = data;
                }
            },
            async requestGetCoupon(param) {
                const today_ticket = this.today_ticket;
                const week_ticket = this.week_ticket;
                const ticket_num = this.ticket_num;
                const {id, num, type, index} = param;
                const {data} = await this.$http.get(API.vipApi('receiveTicket'), {id}, false);
                if (data) {
                    const {notice} = data;
                    if (type === 1) {
                        today_ticket.tickets[index].is_receive = 1;
                        today_ticket.notice = notice;
                        this.today_ticket = {...today_ticket};
                    }
                    if (type === 2) {
                        week_ticket.tickets[index].is_receive = 1;
                        week_ticket.notice = notice;
                        this.week_ticket = {...week_ticket};
                    }
                    this.$toast('领取成功');
                    this.ticket_num = ticket_num + (num ? num : 0);
                }
            },
            setApp() {
                if (!platform.isApp) return;
                //标记特殊来源需要返回到app首页
                let {back_app} = this.$route.query;
                const buttonOpts = [{style: 1, type: 'cart'}];
                setTimeout(() => {
                    bridge.setRightTopButton({items: buttonOpts});
                    bridge.setNavigationBarStyle({style: 1, bar_color: '#2E3748'});
                }, 200);
                this.eventListenerApp('APP_E3', () => {
                    this.openUrl({path: 'ddxq://cart/daimaicai'});
                });
                back_app && this.eventListenerApp('APP_E6', () => {
                    this.openUrl({path: 'ddxq://product/home'});
                }, true);
            },
            cardInfoUpdate(cards, renew_remain) {
                let card_name = '';
                let card_price = '';
                let card_origin_price = '';
                let card_rebate = '';
                let card_continue = (0 < renew_remain && renew_remain <= 15);
                let is_activity = false;    //是否有优惠活动
                let m_index = 0;

                if (!!cards && !!cards.length) {
                    card_rebate = cards[0].discount;
                    cards.map((card, c) => {
                        if (card_rebate >= cards[c].discount) {
                            card_rebate = cards[c].discount;
                            m_index = c;
                        }
                    });
                    card_name = cards[m_index].name;
                    card_price = cards[m_index].price;
                    card_origin_price = cards[m_index].origin_price;
                    if (card_rebate <= 9) {
                        card_rebate = card_rebate + '折'
                    } else {
                        card_rebate = ''
                    }
                    is_activity = card_price !== card_origin_price;
                }
                this.card_float = false;

                this.is_activity = is_activity;
                this.card_rebate = card_rebate;
                this.card_name = card_name;
                this.card_price = card_price;
                this.card_origin_price = card_origin_price;
                this.card_continue = card_continue;
            },
            eventVipScroll() {
                let page_top = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop || (this.domVip ? this.domVip.scrollTop : 0);
                if (!page_top) return;
                this.scrollTabsFloat(page_top);
                this.scrollCardBottom(page_top);
                this.getMoreProduct(page_top);
            },
            //商品导航tabs显示隐藏
            scrollTabsFloat(page_top) {
                const tabs_top = this.tabs_top;
                const tabs_pos = page_top >= this.tabs_top && tabs_top;
                if (this.tabs_float !== tabs_pos) {
                    this.tabs_float = tabs_pos;
                }
            },
            //底部开通入口显示隐藏
            scrollCardBottom(page_top) {
                const card_top = this.card_top;
                const card_float = this.card_float;
                const card_continue = this.card_continue;
                const status = this.status;

                const card_pos = page_top >= card_top;
                if (card_float !== card_pos) {
                    if (status !== 1) {
                        this.card_float = card_pos;
                    } else {
                        this.card_float = card_continue;
                    }
                }
            },
            //获取分页数据
            getMoreProduct(page_top) {
                const isMore = this.is_more;
                const tabsIndex = this.tabs_index;
                const going = this.going;
                const page = this.page;
                const windowHeight = document.documentElement.clientHeight;
                const scrollHeight = document.documentElement.scrollHeight;
                if (!tabsIndex && isMore && !going) {
                    if (page_top + windowHeight + 10 < scrollHeight) return;
                    this.going = true;
                    this.requestVipProducts({page: page + 1})
                }
            },
            onSelectTab(nav) {
                const {index, id} = nav;
                this.tabs_index = index;
                this.is_more = false;
                if (index) {
                    this.requestCateProducts(id);
                } else {
                    this.requestVipProducts({page: 1});
                }
            },
            eventVipPay(param) {
                const {position} = param;
                const card_continue = this.card_continue;
                const is_vip = this.is_vip;
                if (!card_continue) {
                    if (position === 'speed') {
                        this.log({
                            cid: 'my_vip',
                            aid: 'bonus_point_accelerate',
                            index: is_vip
                        });
                    } else {
                        this.log({
                            cid: 'my_vip',
                            aid: 'open_vip',
                            name: position,
                            index: is_vip
                        });
                    }
                } else {
                    this.log({
                        cid: 'my_vip',
                        aid: 'continue_vip',
                        name: position,
                        index: is_vip
                    });
                }
                this.openUrl({path: '/vip/pay', query: {invite_code: this.invite_code}});
            },
            eventUseCoupon(coupon) {
                const {native_url, h5_url} = coupon;
                if (platform.isApp) {
                    this.openUrl({path: native_url || h5_url || 'ddxq://product/home'})
                }
            },
            eventGetCoupon(coupon, options) {
                const {id, num} = coupon;
                const isVip = this.is_vip;
                const _that = this;
                if (isVip) {
                    this.requestGetCoupon({id, num, ...options});
                } else {
                    this.$confirm({
                        content: '开通绿卡，立享此券',
                        submitText: '开卡领券',
                        success() {
                            _that.log({
                                cid: 'my_vip_novip_coupon',
                                aid: 'open_vip'
                            });
                            _that.openUrl({path: '/vip/pay', query: {invite_code: _that.invite_code}});
                        }
                    });
                }
            },
            //积分说明
            eventGoIntegral() {
                this.openUrl({path: 'https://maicai.api.ddxq.mobi/info/single?alias=integration_point'});
            },
            animatePos(num) {
                const nowTop = document.body.scrollTop || document.documentElement.scrollTop;
                if (nowTop > num) {
                    let prevTop = nowTop;
                    navInterval = setInterval(() => {
                        const osTop = document.body.scrollTop || document.documentElement.scrollTop;
                        const speed = Math.floor((osTop - num) / 2);

                        //中途有滑动行为停止
                        if (prevTop >= osTop) {
                            prevTop = osTop;
                        } else {
                            clearInterval(navInterval);
                        }

                        document.documentElement.scrollTop = speed + num;
                        document.body.scrollTop = speed + num;
                        if (speed === 0) {
                            //清除定时器
                            clearInterval(navInterval);
                        }
                    }, 50)
                }
                if (nowTop < num) {
                    navInterval = setInterval(() => {
                        const osTop = document.body.scrollTop || document.documentElement.scrollTop;
                        const osHeight = document.body.scrollHeight || document.documentElement.scrollHeight;
                        const osClient = window.clientHeight || document.documentElement.clientHeight;
                        let speed = Math.floor((num - osTop) / 2);
                        if (speed === -1) {
                            speed = 0
                        }
                        document.documentElement.scrollTop = num - speed;
                        document.body.scrollTop = num - speed;
                        if (speed === 0 || (osTop + osClient + 1 >= osHeight)) {
                            //清除定时器
                            clearInterval(navInterval);
                        }
                    }, 50)
                }
            },
            eventSaveChange() {
                this.showSave = !this.showSave;
            },
            eventFriendsChange() {
                if (this.showFriends) {
                    this.waitInit(() => {
                        this.showGuide = true;
                    });
                }
                this.showFriends = !this.showFriends;
            },
            eventFriends() {
                this.eventFriendsChange();
            },
            eventGuideMask() {
                this.activeScroll();
                this.showGuide = false;
            },
            eventGuideSend() {
                //todo open send
                this.log({
                    cid: 'my_vip',
                    aid: 'share_friends'
                });
                this.showGuide = false;
                this.activeScroll();
                this.openUrl({path: 'vip/friends'})
            }
        }
    }
