<!--目前用于vip页-->
<template>
    <div class="tab-content" :class="{'z-index':tabsAllShow}">
        <div class="more-part" @click="_eventMore(tabsAllShow)">
            <icon-font name="qiehuanfenlei_huaban" size="36" color="#B5B5B5" class="icon-more"/>
        </div>
        <div class="tab-list" ref="domTabNav">
            <div class="tab-part" :class="{on: tabsIndex===i}" v-for="(item, i) in tabList" :key="i" @click.stop="_eventSelectTab($event,item,i)">
                {{item.name}}
            </div>
        </div>
        <div class="mask-all" v-if="tabsAllShow" @click="_eventMore(tabsAllShow)"></div>
        <div class="tab-all-list" v-if="tabsAllShow">
            <div class="tab-all-part" :class="{on: tabsIndex===i}" v-for="(item, i) in tabList" :key="i" @click="_eventSelectTabAll(i)">
                {{item.name}}
            </div>
        </div>
    </div>
</template>

<script>
    let navTnterval = null;
    export default {
        props: {
            tabList: Array
        },
        data() {
            return {
                domTabNav: null,
                tabsAllShow: false,
                tabsIndex: 0
            }
        },
        mounted() {
            this.domTabNav = this.$refs.domTabNav;
        },
        methods: {
            _eventMore(show) {
                this.tabsAllShow = !show
            },
            _eventSelectTab(event, item, index) {
                clearInterval(navTnterval);
                //输出选中tab信息
                this.$emit('onSelectTab', {...item, index});
                const domTabNav = this.domTabNav;
                const navDomWidth = domTabNav.scrollWidth;
                const navClientWidth = domTabNav.clientWidth;
                const navScrollLeft = domTabNav.scrollLeft;
                const diffWidth = Math.round((navClientWidth - event.target.getBoundingClientRect().width) / 2 + 22);
                const targetOffset = event.target.offsetLeft - diffWidth;
                if (targetOffset < 0 && navScrollLeft > 0) {
                    navTnterval = setInterval(() => {
                        const osLeft = domTabNav.scrollLeft;
                        const speed = Math.floor(osLeft / 2);
                        domTabNav.scrollLeft = speed;
                        if (speed === 0) {
                            //清除定时器
                            clearInterval(navTnterval);
                        }
                    }, 100)
                }
                if (targetOffset > 0) {
                    navTnterval = setInterval(() => {
                        const osLeft = domTabNav.scrollLeft;
                        let speed = Math.floor((targetOffset - osLeft) / 2);
                        if (speed === -1) {
                            speed = 0
                        }
                        domTabNav.scrollLeft = targetOffset - speed;
                        if (speed === 0 || ((navDomWidth - navClientWidth) === osLeft && speed > 0)) {
                            //清除定时器
                            clearInterval(navTnterval);
                        }
                    }, 100)
                }
                this.tabsIndex = index;
            },
            _eventSelectTabAll(index) {
                this.domTabNav.children[index].click();
                this.tabsAllShow = false;
            }
        },
        destroyed() {
            clearInterval(navTnterval);
        }
    }
</script>

